import
uniCalendar
from "../../../../components/uni-calendar/uni-calendar"
export default {
	components: {
		uniCalendar
	},
	data() {
		return {
			taskCalendarList: [],
			dayOfMissionList: [],
			year: '',
			month: '',
			day: '',
		}
	},
	methods: {
		// 日历
		change(e) {
			console.log(e)
			this.year = e.year,
				this.month = e.month,
				this.day = e.date,
				this.taskCalendar()
			this.getDayOfMissionList()
		},
		// 获取任务日历数据
		taskCalendar() {
			uni.request({
				url: 'http://192.168.2.107:8089/backwordSystem/teacher/main/taskCalendar',
				data: {
					year: this.year,
					month: this.month,
				},
				success: (data) => {
					console.log(data)
					this.taskCalendarList = data.data
				}
			})
		},
		// 单日任务详情列表
		getDayOfMissionList() {
			uni.request({
				url: 'http://192.168.2.107:8089/backwordSystem/teacher/main/dayOfMissionList',
				data: {
					month: this.month,
					year: this.year,
					day: this.day,
				},
				success: (data) => {
					console.log(data)
					this.dayOfMissionList = data.data
				}
			})
		},
		// 页面跳转
		linkTo(taskId) {							
			uni.navigateTo({
				url: '../details/operationalDetails?taskId=' + taskId
			})
		}
	},
	created() {}

};
